.game-register {
    padding: 20px;
    padding-top: 0px;
}

.game-register h1 {
    font-family: sans-serif;
    color: gray;
    font-weight: bold;
    margin-bottom: 40px;
}

.game-register .col {
    margin-right: 40px;
}

.game-register label {
    text-align: right;
    width: auto;
}

.game-register input {
    max-width: 300px;
    min-width: 300px;
    border-color: #69c521;
}

.game-register-top {
    text-align: right;
}

.game-register-top>div>span, .game-register-form span.green {
    color: #69c521;
    font-family: sans-serif;
    font-size: 18px;
    font-weight: normal;
}

.game-register-form {
    margin-top: 20px;
}

.game-register-form input[type="radio"] {
    max-width: 20px;
    min-width: 20px;
}

.game-register-form .tribe label {
    margin-right: 10px;
    display: inline-block !important;
}

@media only screen and (max-width: 680px) {
    .game-register h1 {
        margin-bottom: 25px;
    }
    .game-register .col {
        margin-right: 10px;
        margin-bottom: 10px;
        text-align: center;
    }
    .game-register label {
        display: none;
    }
    .game-register input {
        display: unset;
        max-width: 200px;
        min-width: auto;
    }
}